import React from "react";
import { TableCell, TableRow, Typography } from "@material-ui/core";
import error from "../../../assets/images/state/error-icon.svg";
import waiting from "../../../assets/images/state/waiting.svg";
import success from "../../../assets/images/state/success.svg";

const MdCard = ({ classes, item, setDetail }) => {
  return (
    <TableRow
      className={classes.tablerow}
      onClick={() =>
        item?.status?.value === "2" &&
        setDetail((detail) => ({
          ...detail,
          open: true,
          detailData: item,
        }))
      }
    >
      <TableCell className={classes.tableCell}>
        <Typography variant="body1" className={classes.tableValue}>
          {item.date}
        </Typography>
        <Typography
          style={{ fontWeight: 300, fontSize: 16 }}
          className={classes.tableTitle}
        >
          {item.time}
        </Typography>
      </TableCell>
      <TableCell align="center" className={classes.tableCell}>
        <Typography className={classes.title}>
          {" "}
          <Typography
            variant="body1"
            style={{ textDecoration: "underline" }}
            className={classes.tableValue}
          >
            {item.type}
          </Typography>
        </Typography>
      </TableCell>
      <TableCell align="center" className={classes.tableCell}>
        <Typography className={classes.title}>
          <Typography variant="body1" className={classes.tableValue}>
            {item.debit === null ? "-" : item.debit}
          </Typography>
        </Typography>
      </TableCell>
      <TableCell align="center" className={classes.tableCell}>
        <Typography variant="body1" className={classes.tableValue}>
          {item.debit === null ? item.sum : !item.credit ? "-" : item.credit}
        </Typography>
      </TableCell>
      <TableCell align="center" className={classes.tableCell}>
        <Typography variant="body1" className={classes.tableValue}>
          <img className={classes.icon} src={item.logo} alt="coin" />
        </Typography>
      </TableCell>
      <TableCell align="center" className={classes.tableCell}>
        <Typography variant="body1" className={classes.tableValue}>
          <img
            src={
              item.status?.value === "1"
                ? waiting
                : item.status?.value === "2"
                ? success
                : error
            }
            alt="status"
            style={{ width: 20 }}
          />
        </Typography>
      </TableCell>
    </TableRow>
  );
};

export default MdCard;
