import React from "react";
import { Grid, makeStyles, Typography } from "@material-ui/core";
import error from "../../../assets/images/state/error-icon.svg";
import waiting from "../../../assets/images/state/waiting.svg";
import success from "../../../assets/images/state/success.svg";

const XsCard = ({ t, item, setDetail }) => {
  const classes = useStyles();
  return (
    <Grid
      container
      spacing={1}
      className={classes.grid}
      onClick={() =>
        item?.status?.value === "2" &&
        setDetail((detail) => ({
          ...detail,
          open: true,
          detailData: item,
        }))
      }
    >
      <Grid
        item
        xs={4}
        container
        justifyContent="flex-start"
        direction="column"
        alignItems="center"
      >
        <Typography variant="body1" className={classes.title}>
          {t("date")}
        </Typography>
        <Typography variant="body1" className={classes.value}>
          {item.date}
        </Typography>
        <Typography
          style={{ fontWeight: 300, fontSize: 16 }}
          className={classes.value}
        >
          {item.time}
        </Typography>
      </Grid>
      <Grid
        item
        xs={4}
        container
        justifyContent="flex-start"
        direction="column"
        alignItems="center"
      >
        <Typography variant="body1" className={classes.title}>
          {t("OperationType")}
        </Typography>
        <Typography variant="body1" className={classes.value}>
          {item.type}
        </Typography>
      </Grid>
      <Grid
        item
        xs={4}
        container
        justifyContent="flex-start"
        direction="column"
        alignItems="center"
      >
        <Typography variant="body1" className={classes.title}>
          {t("incomes")}
        </Typography>
        <Typography variant="body1" className={classes.value}>
          {item.debit === null ? "-" : item.debit}
        </Typography>
      </Grid>
      <Grid
        item
        xs={4}
        container
        justifyContent="flex-start"
        direction="column"
        alignItems="center"
      >
        <Typography variant="body1" className={classes.title}>
          {t("expenses")}
        </Typography>
        <Typography variant="body1" className={classes.value}>
          {item.debit === null ? item.sum : !item.credit ? "-" : item.credit}
        </Typography>
      </Grid>
      <Grid
        item
        xs={4}
        container
        justifyContent="flex-start"
        direction="column"
        alignItems="center"
      >
        <Typography variant="body1" className={classes.title}>
          {t("PaymentSystemShort")}
        </Typography>
        <img className={classes.icon} src={item.logo} alt="coin" />
      </Grid>
      <Grid
        item
        xs={4}
        container
        justifyContent="flex-start"
        direction="column"
        alignItems="center"
      >
        <Typography variant="body1" className={classes.title}>
          {t("state")}
        </Typography>
        <img
          src={
            item.status?.value === "1"
              ? waiting
              : item.status?.value === "2"
              ? success
              : error
          }
          alt="status"
          style={{ width: 20 }}
        />
      </Grid>
    </Grid>
  );
};

export default XsCard;

const useStyles = makeStyles((theme) => ({
  title: {
    fontWeight: "normal",
    fontSize: "16px",
    lineHeight: "19px",
    textAlign: "center",
    color: "#64748B",
    marginBottom: 8,
    whiteSpace: "nowrap",
  },

  value: {
    fontWeight: "600",
    fontSize: "16px",
    lineHeight: "19px",
    color: "#000000",
  },

  icon: {
    width: 32,
    height: 32,
    objectFit: "contain",
  },

  grid: {
    borderBottom: "1px solid #E7EAEE",
    marginBottom: 12,
    paddingBottom: 16,

    "&:last-child": {
      borderBottom: "none",
    },
  },
}));
