/* eslint-disable eqeqeq */
import React, { useState } from "react";
import {
  Dialog,
  DialogContent,
  makeStyles,
  Typography,
  Button,
  IconButton,
  Snackbar,
} from "@material-ui/core";
import CopyToClipboard from "react-copy-to-clipboard";
import copyIcon from "../../../../assets/images/state/document-copy.svg";
import { Box } from "@mui/system";
import { Alert } from "@mui/material";
import {
  Eighth,
  Fifth,
  First,
  Fourth,
  Second,
  Seventh,
  Sixth,
  Third,
} from "./Variants";

const HistoryDetail = ({ t, item, open, setOpen }) => {
  const classes = useStyles();
  const [copy, setCopy] = useState(false);

  return (
    <Dialog
      open={open}
      onClose={() => setOpen((detail) => ({ ...detail, open: false }))}
      classes={{ paper: classes.paper }}
    >
      <DialogContent className={classes.DialogContent}>
        <Snackbar
          open={copy}
          autoHideDuration={800}
          onClose={() => setCopy(false)}
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
        >
          <Alert severity="success" color="success" variant="filled">
            {t("copied")}!
          </Alert>
        </Snackbar>
        <Typography variant="h2" className={classes.title}>
          {t("paymentCheck")}
        </Typography>
        <ul style={{ listStyle: "none" }}>
          <Typography variant="h5" component="li">
            {t("OperationType")}
            <div></div>
            <span>{item?.type}</span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("DateTime")}
            <div></div>
            <span>
              {item?.date}
              {"  "}
              {item?.time}
            </span>
          </Typography>
          {item.operation_type == 1 && <First item={item} t={t} />}
          {item.operation_type == 2 && <Second item={item} t={t} />}
          {item.operation_type == 3 && <Third item={item} t={t} />}
          {item.operation_type == 4 && <Fourth item={item} t={t} />}
          {item.operation_type == 5 && <Fifth item={item} t={t} />}
          {item.operation_type == 6 && <Sixth item={item} t={t} />}
          {item.operation_type == 7 && <Seventh item={item} t={t} />}
          {item.operation_type == 8 && <Eighth item={item} t={t} />}
          <Typography variant="h5" component="li">
            {t("Currency")}
            <div></div>
            <span>{item?.currency}</span>
          </Typography>
          <Typography variant="h5" component="li">
            {t("state")}
            <div></div>
            <span>{item?.status?.name}</span>
          </Typography>

          {item?.requisite && (
            <Box
              Box
              display="flex"
              align="center"
              flexDirection="column"
              justifyContent="center"
            >
              <Typography variant="h5">{t("Props")}</Typography>
              <Box
                display="flex"
                alignItems="flex-start"
                onClick={() => setCopy(true)}
              >
                <input
                  value={item?.requisite}
                  style={{ marginBottom: 10 }}
                  className={classes.textField}
                />
                <CopyToClipboard text={item?.requisite}>
                  <IconButton style={{ marginLeft: 16, width: "fit-content" }}>
                    <img src={copyIcon} alt="" />
                  </IconButton>
                </CopyToClipboard>
              </Box>
            </Box>
          )}
          {item.batch && (
            <Box
              display="flex"
              align="center"
              flexDirection="column"
              justifyContent="center"
            >
              <Typography variant="h5">{t("ReceiptNumber")}</Typography>
              <Box
                display="flex"
                alignItems="flex-start"
                onClick={() => setCopy(true)}
              >
                <input value={item?.batch} className={classes.textField} />
                <CopyToClipboard text={item?.batch}>
                  <IconButton style={{ marginLeft: 16, width: "fit-content" }}>
                    <img src={copyIcon} alt="" />
                  </IconButton>
                </CopyToClipboard>
              </Box>
            </Box>
          )}
          {item.memo && (
            <Box
              display="flex"
              align="center"
              flexDirection="column"
              justifyContent="center"
            >
              <Typography variant="h5">{t("note")}</Typography>
              <Box
                display="flex"
                alignItems="flex-start"
                onClick={() => setCopy(true)}
              >
                <input value={item?.memo} className={classes.textField} />
                <CopyToClipboard text={item?.memo}>
                  <IconButton style={{ marginLeft: 16, width: "fit-content" }}>
                    <img src={copyIcon} alt="" />
                  </IconButton>
                </CopyToClipboard>
              </Box>
            </Box>
          )}
        </ul>
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={() => setOpen((detail) => ({ ...detail, open: false }))}
        >
          {t("close")}
        </Button>
      </DialogContent>
    </Dialog>
  );
};

export default HistoryDetail;

const useStyles = makeStyles((theme) => ({
  paper: {
    borderRadius: 16,
    width: "100%",
  },

  DialogContent: {
    padding: "40px 31px!important",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },

    "& ul": {
      "& li": {
        fontWeight: 300,
        marginBottom: 16,
        display: "flex",
        justifyContent: "space-between",
        whiteSpace: "nowrap",
        alignItems: "baseline",

        "& div": {
          width: "100%",
          height: "fit-content",
          borderBottom: "2px dotted #B8C0CC",
          margin: "0 3px",
        },

        "& span": {
          fontWeight: 500,
        },
      },
    },

    "& button": {
      width: "100%",
      height: 56,
      marginTop: 16,
    },
  },

  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },

  title: {
    textAlign: "center",
    color: "#000",
    fontWeight: 500,
    letterSpacing: "-0.04em",
    lineHeight: "56px",
    marginBottom: 24,

    [theme.breakpoints.down(992)]: {
      fontSize: 48,
    },

    [theme.breakpoints.down(768)]: {
      fontSize: 34,
      lineHeight: "40px",
    },
  },

  subtitle: {
    color: "#64748B",
    marginLeft: 10,
  },
  textField: {
    marginTop: 10,
    border: "1px solid #B8C0CC",
    borderRadius: 8,
    width: "100%",
    padding: "18px 20px",
    outline: "none",
    fontSize: 16,
  },
}));
