import { Typography } from "@material-ui/core";
import React from "react";

export const First = ({ item, t }) => {
  return (
    <>
      <Typography variant="h5" component="li">
        {t("Amount")}
        <div></div>
        <span>
          {item?.sum} {item?.currency}
        </span>
      </Typography>
      <Typography variant="h5" component="li">
        {t("amountToBeCredited")}
        <div></div>
        <span>{item?.debit} USDT</span>
      </Typography>
      {item.commission && (
        <Typography variant="h5" component="li">
          {t("Commission")}
          <div></div>
          <span>
            {item?.commission} {item.currency}
          </span>
        </Typography>
      )}
    </>
  );
};

