import { Container, useTheme, useMediaQuery } from "@material-ui/core";
import { useFormik } from "formik";
import React, { useEffect, useState } from "react";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import { useDispatch, useSelector } from "react-redux";
import {
  getOperationList,
  getOperationsTypes,
} from "../../../redux/reducers/Operations";
import { Pagination, Stack } from "@mui/material";
import HistoryDetail from "./HistoryDetail";
import Form from "./Form";
import NoneResults from "./NoneResults";
import ActionTable from "./Table";
import { Box } from "@mui/system";
import XsCard from "./XsCard";
import { getBalances } from "../../../redux/reducers/myProfile";

const History = ({ t }) => {
  const dispatch = useDispatch();
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));

  useEffect(() => {
    dispatch(getOperationList());
    dispatch(getOperationsTypes());
    dispatch(getBalances());
    // eslint-disable-next-line
  }, []);

  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);

  const { OperationsHistoryList, operationsTypes } = useSelector(
    ({ OperationsReducer }) => OperationsReducer
  );

  const OperationsHistory = Object.values(
    typeof OperationsHistoryList.operations === "object" &&
      OperationsHistoryList.operations
  );

  useEffect(() => {
    setCount(Math.ceil(OperationsHistoryList.total / 20));
  }, [OperationsHistoryList]);

  const formik = useFormik({
    initialValues: {
      operation_type: "",
      date_from: "",
      date_to: "",
      page: "",
    },
    onSubmit: (values) => {
      values.page = page;
      dispatch(getOperationList(values));
      setPage(1);
    },
  });

  const changeHandler = (event, value) => {
    setPage(value);
    dispatch(
      getOperationList({
        page: value,
        operation_type: formik.values.operation_type,
        date_from: formik.values.date_from,
        date_to: formik.values.date_to,
      })
    );
  };

  const [detail, setDetail] = useState({ open: false, detailData: [] });

  return (
    <section style={{ color: "#fff" }}>
      <Container maxWidth="lg">
        <Balance history />
        <CabinetTop title={t("operationHistory")} />
        <Form
          operationsTypes={operationsTypes}
          formik={formik}
          setPage={setPage}
          t={t}
        />
        {!OperationsHistory.length ? (
          <NoneResults t={t} />
        ) : tablet ? (
          <Box
            sx={{
              backgroundColor: "#fff",
              padding: "16px",
              boxShadow: "0px 1px 0px #E2E8F0",
              borderRadius: "12px",
              marginTop: "16px",
            }}
          >
            {OperationsHistory?.map((item) => (
              <XsCard setDetail={setDetail} item={item} t={t} />
            ))}
          </Box>
        ) : (
          <ActionTable
            t={t}
            OperationsHistory={OperationsHistory}
            setDetail={setDetail}
          />
        )}
        <HistoryDetail
          t={t}
          item={detail.detailData}
          open={detail.open}
          setOpen={setDetail}
        />
        {count > 0 && (
          <Stack
            spacing={2}
            mt={"50px"}
            sx={{
              "& nav": {
                display: "flex",
                justifyContent: "center",

                "& button": {
                  minWidth: { xs: "20px!important", md: "40px!important" },
                },
              },
            }}
          >
            <Pagination
              count={count}
              size="large"
              page={page}
              onChange={changeHandler}
            />
          </Stack>
        )}
      </Container>
    </section>
  );
};

export default History;
